	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/corona-sdk-solving-json-decode-error-with-utf-8-files/index.md absPath of file >>> MarkdownRemark","html":"<p>While working with JSON-encoded data files, I had some problems getting the darn things to work.  Apart from an unhelpful error message, the Corona simulator gave me no clues as to what was wrong. Turned out that the problem was actually to do with the UTF-8 encoding. And in the end, what fixed it was changing the UTF-8 encoding to <strong>\"UTF-8 without BOM\"</strong> - the default UTF-8 encoding just didn't cut it.</p>\n<img class=\"alignnone size-full wp-image-1457\" title=\"Corona SDK\" src=\"http://uploads.psyked.co.uk/2011/11/corona-ribbon.png\" alt=\"\" width=\"500\" height=\"280\">","frontmatter":{"title":"Corona SDK: Solving JSON decode error with UTF-8 files.","date":"November 14, 2011","tags":["encoding"]}}},"pathContext":{}}

